<template>
    <div id="player"></div>
    <button id="unmuteButton" @click="yt_unmute()">UnMute</button>
  <div class="header-image">
    <div class="header">
      <p class="no-wrap"> 2024.11.09 SAT 14:40 </p>
      <p class="no-wrap"> 평택 제이앤제이 아트컨벤션 </p>
      <p> 제이드 팰리스 홀 </p>
    </div>
  </div>
  <img src="~@/assets/images/hands_image.png" class="hands" />

</template>

<script>
export default {
  name: "HeaderImage",
  data(){
    return {
      youtubeVideoId: '',
      player: null,
      VIDEO_ID :'5Sx7xqv_oYk?si=7S3iL3ypQKjekKr3',
    };
  },
  computed:{
    youtubeEmbedUrl(){
      return `https://www.youtube.com/embed/0yBnIUX0QAE?autoplay=1&loop=1`;
    }
  },
  mounted(){
    this.loadYouTubeAPI();
  },
  methods:{
    loadYouTubeAPI(){
      if (typeof window.YT === "undefined"){
        const tag = document.createElement("script");
        tag.src = "https://www.youtube.com/iframe_api";
        const firstScriptTag = document.getElementsByTagName("script")[0];
        firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);

        console.log("By onYouTubeIframeAPIReady")
        window.onYouTubeIframeAPIReady = this.createPlayer;
      }
      else{
        console.log("stanalone")
        this.createPlayer();
      }
    },
    createPlayer(){
      console.log("Create Player")
      this.player = new window.YT.Player("player", {
        height: "360",
        width: "640",
        videoId: this.VIDEO_ID,
        events:{
          'onReady': this.onPlayerReady,
          'onStateChange': this.onPlayerStateChange,
        },
      });
      console.log(this.player)
    },
    // 플레이어가 준비되었을 때 호출
    onPlayerReady(event) {
      console.log("YouTube player is ready.",event);
    },
    // 플레이어 상태가 변경되었을 때 호출
    onPlayerStateChange(event) {
      console.log("Player state changed:", event.data);
    },
    yt_unmute(){
      console.log(this.player)
      if (this.player){
        console.log(this.player.isMuted())
        this.player.unMute();
      }
    },
  },
};
</script>

